@media screen and (min-width: 500px) {
  .container {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  
  .Rank {
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    margin-top: 80px;
    overflow:visible;
    width: 850px;
  }

  .filters {
    grid-column: span 3;
    display: flex;
    width: 100%;
    justify-content: flex-start;
    gap: 16px;
  }

  .MuiButton-root {
    display: none !important;
  }

}

@media screen and (max-width: 500px) {

  :root {
    --screen-height: -webkit-fill-available
  } 
  
  .Rank {
    display: grid;
    height: 100%;
    overflow: hidden;
    grid-template-rows: 30px 1fr;
    grid-template-columns: 400px;
    row-gap: 16px;
    justify-content: center;
    align-items: center;
    justify-items: center;
    margin-top: 16px;
  }

  .filters {
    grid-column: span 1;
    justify-self: end;
    display: flex;
    justify-content: flex-end;
    gap: 16px;
  }

  #artist-filter {
    display: none;
  }

  #year-filter {
    display: none;
  }

  #ranking-filter {
    display: none;
  }

  .container {
    height: 100%;
    overflow: hidden;
  }

  .MuiButton-root {
    margin-right: 24px !important;
  }

  #rank-page-container {
    height: -webkit-fill-available;
    max-height: 100vh;
  }

}
